import React, { Fragment } from "react";
import { Button } from "antd";
import {
    ModalContent,
    ModalOpenButton,
    useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";
import { AccessControlTypes, AccessControl, Feature } from "types";
import BuyLicenseAlertBox from "../ui/BuyLicenseAlertBox";
import useAppStore from "components/context/app/Hooks";
import { useTranslation } from 'react-i18next';

export default function EndpointModal() {
    const { t } = useTranslation();
    const { licensed } = useAppStore()
    const { onOk, form } = useModalContext();

    function handleOk() {
        onOk("/accessControl", "create", {} as AccessControl);
    }

    return (
        <Fragment>
            <ModalOpenButton requiredRoles={["Administrator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
                <Button children={t('accessControl.create')} />
            </ModalOpenButton>
            <ModalContent title={t('accessControl.createTitle')} onClickOk={handleOk} formName="create_access_control_form">
                <Form schemaName="accessControl">
                    <FormContentBase
                        name="create_access_control_form"
                        form={form}
                        preserve={false}
                        defaultValues={{
                            url: "",
                            method: "GET"
                        }}
                    />
                    {!licensed(Feature.Api) && <BuyLicenseAlertBox />}
                </Form>
            </ModalContent>
        </Fragment>
    );
}
